<template>
  <div class="vboard-wrapper">
    <div class="vboard">
      <VBoardItem 
        :game-status="gameStatus" 
        v-for="field in fields" :field="field" 
        :key="'item-' + field.id" 
        @selectField="selectField($event)" />
    </div>

    <p class="difficult">Сложность: <strong>{{ difficult }}</strong></p>
    <p class="win" v-if="isWin">Поздравляем! Продолжаем играть!</p>
    <p class="fail" v-if="isFail">Вы проиграли. Попробуйте ещё раз!</p>

    <button class="btn" @click="start" :disabled="!canStartGame">Старт</button>
  </div>
</template>

<script>

import { ref } from "vue";
import VBoardItem from "@/components/VBoardItem.vue";
import useGameInit from "@/components/composables/useGameInit.js";
import useGameStart from "@/components/composables/useGameStart.js";
import useGameProcess from "@/components/composables/useGameProcess.js";
import { GAME_STATUS } from "@/constants";

export default {
  name: 'VBoard',
  props: {},

  components: {
    VBoardItem,
  },

  setup() {
    const number = 25;
    const gameStatus = ref(GAME_STATUS.NONE);

    const { difficult, fields, init } = useGameInit(number);

    const { start, canStartGame } = useGameStart(init, fields, difficult, number, gameStatus);

    const { selectField, isWin, isFail } = useGameProcess(fields, gameStatus, difficult, start);

    return {
      difficult,
      fields,
      number,
      init,
      start,
      gameStatus,
      canStartGame,
      selectField,
      isWin,
      isFail
    }
  },
};
</script>

<style scoped>
.vboard-wrapper {
  margin-bottom: 100px;
}

.vboard {
    width: 300px;
    background: #eee;
    margin: 0 auto;
}

.btn {
  background: #42b983cc;
  color: #fff;
  border: none;
  outline: none;
  border-radius: 2px;
  padding: 10px 30px;
  margin: 10px 0;
  cursor: pointer;
  transition: all .5s;
}

.btn:hover {
  background: #42b983;
}

.btn:disabled {
  opacity: .5;
}

.win {
  color: #42b983;
}

.fail {
  color: #ff000099;
}
</style>